import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import CloseIcon from '@material-ui/icons/Close';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import Link from '@material-ui/core/Link';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';

import Download from '@material-ui/icons/CloudDownload';
import { connect } from 'react-redux';

const useStyles = theme => ({
   card: {
      position: 'relative',
      maxWidth: 345,
      backgroundColor: '#ededed'
   },
   CardHeader:{
      backgroundColor: 'rgb(160,60,45)',
      color: 'rgb(255,255,255)'
   },
   subtitle1:{
      color: 'rgb(160,60,45)'
   },
   body2:{
      color: 'rgb(0,0,0)' // 140,185,160
   },
   media: {
      height: 0,
      paddingTop: '56.25%', // 16:9
   },
   expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
         duration: theme.transitions.duration.shortest,
      }),
   },
   expandOpen: {
      transform: 'rotate(180deg)',
   },
   avatar: {
      backgroundColor: red[500],
   },
});

const TabContainer = (props) => (
    <Typography component="div" className="pa-0">
        { props.children }
    </Typography>
);

let SideNav = class SideNav extends Component{
   state = {
      expanded: false,
      tab: 0,
   }

   handleExpandClick = () => {
      this.setState({
         expanded: !this.state.expanded
      });
   }

   handleTabToggle = (event, tab) => {
      this.setState({ tab });
   };
   render(){
      const {classes, dataInfo } = this.props;
      const {expanded,tab } = this.state;
      return(
         <Card className={classes.card}>
            { this.props.sideNav &&
               <Fragment>
                  <CardHeader

                     action={
                           <IconButton aria-label="cerrar" onClick = {() => this.props.setInfoObras({
                              info:false,
                              id: '',
                              nombre: '',
                              licitacion: '',
                              descripcio: '',
                              clasifica: '',
                              imagen: ''
                           })}>
                              <CloseIcon/>
                           </IconButton>
                     }
                     title = { dataInfo.nombre }
                     subheader =  { dataInfo.licitacion }
                     className={classes.CardHeader}
                     />
                  <CardMedia
                     className={classes.media}
                     image = { dataInfo.imagen }
                     title="Paella dish"
                     />
                  <CardContent>
                     <Tabs value={tab} onChange={this.handleTabToggle}>
                        <Tab label="Descripción" classes={{ root: classes.tabRoot }} />
                        <Tab label="Documentación" classes={{ root: classes.tabRoot }} />
                     </Tabs>
                     { tab == 0 &&
                        <TabContainer>
                           <Typography variant="subtitle1" component="p" className={classes.subtitle1}>
                              Descripción:
                           </Typography>
                           <Typography variant="body2" component="p" className={classes.body2}>
                               { dataInfo.descripcio }
                           </Typography>
                        </TabContainer>
                     }
                     { tab == 1 &&
                        <TabContainer>
                           <List>
                              <ListItem component={Link} to={`/static/docs/gaceta/${dataInfo.cvegeo}.pdf`}>
                                 <ListItemAvatar>
                                    <Avatar><Download /></Avatar>
                                 </ListItemAvatar>
                                 <ListItemText
                                    primary="Publicación"
                                 />
                              </ListItem>
                              <ListItem  component={Link} to='/static/docs/fallo/${dataInfo.cvegeo}.pdf'>
                                 <ListItemAvatar>
                                    <Avatar><Download /></Avatar>
                                 </ListItemAvatar>
                                 <ListItemText
                                    primary="Fallo"
                                 />
                              </ListItem>
                           </List>
                        </TabContainer>
                     }
                  </CardContent>
               </Fragment>
            }
         </Card>
      );
   }
}



function mapStateToProps(state) {
   return {
      sideNav: state.sideNav,
      dataInfo: state.data
   };
}

SideNav = connect(mapStateToProps)(SideNav);
export default withStyles(useStyles)(SideNav);
